import React, { useState, useEffect } from "react";
import Layout from "./Layout";
import { contentsHandler } from "../util/handler";

const styleSheet = {
  container: {
    background: "black",
    height: "1000px",
    color: "white",
    textAlign: "center",
    paddingTop: "30px",
    fontSize: "4em"
  }
};
const View = ({ data }) => {
  const [lyrics, setLyrics] = useState(contentsHandler(data));
  const dataHandler = () => {
    console.log(lyrics[0].contents[0]);
    return lyrics[0].contents[0];
  };
  return (
    <Layout title="View">
      <div style={styleSheet.container}>{dataHandler()}</div>
    </Layout>
  );
};

export default View;
